import { Table, Container, Row, Col } from "react-bootstrap";
import { useSelector, useDispatch } from "react-redux";
import { increase } from "../store/userSlice";
import { addCount, delCount, delItem } from "../store/cartSlice";
import { Link } from "react-router-dom";

function Cart() {
  let state = useSelector((state) => {
    return state;
  });
  let dispatch = useDispatch(); //store   
  /*    */
  let totalPrice = [0];
  let i = 0;

  for (i = 0; i < state.cart.length; i++) {
    let totalAdd = state.cart[i].price * state.cart[i].count;
    totalPrice.push(totalAdd);
  }
  const sum = totalPrice.reduce((x, y) => x + y);
  return (
    <>
      <Container>
        {state.user.name} {state.user.age}  
        <button
          onClick={() => {
            dispatch(increase(10));
          }}
        >
          
        </button>
        <Row className="mt-5">
          <Col className="mt-5">
            <Table striped bordered hover>
              <thead>
                <tr>
                  <th>No</th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                  <th></th>
                </tr>
              </thead>
              <tbody>
                {state.cart.map((a, i) => (
                  <tr key={i}>
                    <td>{state.cart[i].id}</td>
                    <td>
                      <img
                        style={{ width: 100 }}
                        src={state.cart[i].img}
                        alt="image"
                      />
                    </td>
                    <td>{state.cart[i].name}</td>

                    <td>
                      <button
                        onClick={() => {
                          dispatch(delCount(state.cart[i].id));
                        }}
                      >
                        -
                      </button>
                      {state.cart[i].count} {/*    */}
                      <button
                        onClick={() => {
                          dispatch(addCount(state.cart[i].id));
                        }}
                      >
                        +
                      </button>
                    </td>
                    <td>{state.cart[i].count * state.cart[i].price}</td>
                    <td
                      onClick={() => {
                        dispatch(delItem(state.cart[i].id));
                      }}
                    >
                      
                    </td>
                  </tr>
                ))}
                <tr>
                  <td colSpan={6}>
                    : {sum}
                    <Link to="/Login"></Link>
                  </td>
                </tr>
              </tbody>
            </Table>
          </Col>
        </Row>
      </Container>
    </>
  );
}

export default Cart;
